/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>What' up on the <a title=\"PapaScott: Windmills over Lüllau\" href=\"http://www.papascott.de/2003/09/27/2600.php\">anti-windmill</a> front? Our informal group organized last week. We named ourselves \"Lüllauer Gegenwind\", selected our neighbor as speaker, and my wife as treasurer. The group printed up flyers to inform the village of the informational meeting held by the windmill investors. They expected 20 people, but 120 showed up (our village has a population of 400), and the meeting was <a title=\"Lüllau: Sturm gegen neue Windräder\" href=\"http://www.abendblatt.de/daten/2003/10/13/217568.html\">written up</a> in the Hamburger Abendblatt. We've been getting some help from a similar group in Dahlenburg, <a title=\"Windkraft mit Vernunft\" href=\"http://www.windkraftmitvernunft.de/\">'Windkraft mit Vernunft'</a>.</p>\n<p>The group will be meeting with farmers this week to try to convince them that wind energy is not the secure source of income that the wind investment firms make it out to be. We'll be following the situation at the city level (where the zoning situation is somewhat muddled) and the state level (where a bill proposed by the new CDU/FDP government will increase the required distance between windmills and residential areas). We're unsure whether our village is 'residential' or 'mixed used'.</p>\n<p>I'll probably let my wife do most of the windmill campaigning, since Christopher is too young to be left alone evenings. I've offered to set up a weblog for the group, but they haven't taken me up on it yet.</p>","fields":{"title":"Windmill Front","date":"15 Oct 2003","url":"https://papascott.de/2003-10-15-windmill-front/"}}},"pathContext":{"slug":"/2003-10-15-windmill-front/","prev":{"fields":{"title":"PHP: Multi-Byte String Functions","date":"15 Oct 2003","slug":"/2003-10-15-php-multi-byte-string-functions/"}},"next":{"fields":{"title":"The world as we know it will *not* end","date":"16 Oct 2003","slug":"/2003-10-16-the-world-as-we-know-it-will-not-end/"}}}}

/***/ })

